import './reset.less';
import './index.less';

import React from 'react';
import ReactDOM from 'react-dom';
import Modal from 'react-modal';
import $ from 'zepto';
import UrlParse from 'url-parse';
import ActivityRewards from './rewards';
import ActivityUserinfo from './userinfo';

class ActivityChange extends React.Component {
  constructor(props) {
    super(props);
    this.getToken.call(this);
    this.tokenObj = {
      flag:0
    };

    this.state = {
      modalIsOpen: false,
      msg: ''
    }

    this.initActivity = this.initActivity.bind(this);
    this.changeAction = this.changeAction.bind(this);
    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  componentDidMount() {
    this.initActivity();
  }

  getToken() {
    let self = this;
    let url = UrlParse(location.href, true);
    location.href = url.query.protocol + '://fdt/request/getToken';
    window.setToken = function(token) {
      self.tokenObj.flag = 1;
      self.tokenObj.token = token;
    }
  }

  initActivity() {
    var self = this;
    if (self.tokenObj && self.tokenObj.flag === 1) {
      $.ajax({
        url: self.props.source,
        type:'GET',
        dataType:'json',
        data: {auth_token: self.tokenObj.token},
        success: function(data) {
          if (data.meta.code === 200) {
            let activityInfo = data.activityInfo;
            let auth_token = self.tokenObj.token;
            self.setState({
              activityId: activityInfo.activityId,
              authToken: auth_token,
              banner: activityInfo.logo,
              description: activityInfo.description,
              rewards: activityInfo.rewards,
              user: activityInfo.user
            });
          }
        },
        error: function() {
          self.openModal('网络不好~请刷新页面重试!');
        }
      })
    } else if (self.tokenObj && self.tokenObj.flag === 0) {
      setTimeout(function() {
        self.tokenObj.flag = 1;
        self.initActivity();
      }, 500);
    }
  }

  changeAction(rewardId, leftCount, leftCash, changeCode) {
    this.state.rewards.map((reward) => {
      if (reward.id === rewardId) {
        reward.leftCount = leftCount;
      }
    })
    this.state.user.exFlag = 1;
    this.state.user.leftCash = leftCash;
    this.state.user.code = changeCode;
    this.setState({
      rewards: this.state.rewards,
      user: this.state.user
    })
  }

  preventScroll(e) {
    e.preventDefault();
  }

  openModal(msg, cb) {
    $('.ReactModalPortal').on('touchmove', this.preventScroll);
    this.setState({
      modalIsOpen: true,
      msg: msg
    });
    cb && cb();
  }

  closeModal() {
    $('.ReactModalPortal').off('touchmove', this.preventScroll);
    this.setState({
      modalIsOpen: false
    });
  }

  render() {
    const customStyles = {
      overlay: {
        backgroundColor: 'rgba(31, 31, 31, 0.77)'
      },
      content : {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        WebkitTransform: 'translate(-50%, -50%)',
        transform: 'translate(-50%, -50%)',
        borderRadius: '9px',
        width:'228px',
        padding: '25px 25px 9px'
      }
    };
    if (typeof this.state.activityId === 'undefined') {
      return (
        <div>
          <div className="loading">
            <div className="img"></div>
          </div>
          <Modal
            className="rewards-dialog"
            isOpen={this.state.modalIsOpen}
            onRequesetClose={this.closeModal}
            style={customStyles}>
              <p>{this.state.msg}</p>
              <a onTouchStart={this.closeModal}>好的</a>
          </Modal>
        </div>
      )
    }
    return (
      <div>
        <div className="m-activity-banner">
          <img src={this.state.banner} />
        </div>
        <ActivityRewards
          activityId={this.state.activityId}
          authToken={this.state.authToken}
          changeAction={this.changeAction}
          openModal={this.openModal}
          rewards={this.state.rewards} />
        <ActivityUserinfo
          authToken={this.state.authToken}
          user={this.state.user} />
        <div className="m-activity-description">
          <h3>活动说明</h3>
          <p>{this.state.description}</p>
        </div>
        <Modal
          className="rewards-dialog"
          isOpen={this.state.modalIsOpen}
          onRequesetClose={this.closeModal}
          style={customStyles}>
            <p>{this.state.msg}</p>
            <a onTouchStart={this.closeModal}>好的</a>
        </Modal>
      </div>
    );
  }
}

ActivityChange.defaultProps = {

}

ActivityChange.propTypes = {

}

ReactDOM.render(
  <ActivityChange source="/im/activityInfo"/>,
  document.getElementById('J-activity')
);



